.title {
	font-size: 34px;
	color: $dark;
	@include md {
		font-size: 30px; }
	@include sm {
		font-size: 26px; }

	&__white {
		color: $white; }

	&__lg {
		font-size: 48px;
		@include md {
			font-size: 36px; }
		@include sm {
			font-size: 30px; } }

	&__sm {
		font-size: 21px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } } }
